}

.control-label {
  &.control-label-slim {
    font-weight: normal;
    //color: #686868
  }

  &.text-left {
    text-align: left;
  }

}

.row-item-detail.toggled {
  //display: block;
}

.box-list__settings__one strong.inline-block,
.inline-block {
  display: inline-block;
}

.more-options {
  z-index: 5;
}

.box-list__settings {
  display: block;
}
.box-list__preview {
  z-index: auto;
}


.myadmin-dd-empty .dd-list .dd3-content {
  &:hover {
    background-color: #f5f5f5;
  }

  margin-bottom: 0!important;

}


.box-list {
  overflow: hidden;
  //min-height: inherit;
  transition: all 500ms;
  transform: scale(1);

  .item-detail-link {
    position: absolute;
    left: 35px;
    right: 0;
    top: 0;
    bottom: 0;
    z-index: 10;
  }
}


.dd-item-backgroundWrap {
  opacity: 0;
  z-index: -1;

  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0,0,0,0.8);
  content: "";

  transition: all 500ms;
}
.modal-detail-open {
  .dd-item-backgroundWrap {
    opacity : 1;
    z-index : 1000;
  }

  // lze přepnout 0 = neklikatelný
  .dd-item:not(.open-detail) {
    //z-index: 0;
  }
}


.activ-controler {
  transition: opacity 100ms;

}

.dd3-item {
  z-index : 900;
  transition: z-index 500ms;

  &.open-detail {
    z-index : 3000;
  }
}

.dd-item, .dd-empty, .dd-placeholder {
  margin-bottom: 12px;
}

.dd-empty {
  display: none;
}


.dd-item {
  .dd-item-backgroundWrap {
    transition: opacity 500ms;

    opacity: 0;
    z-index: -1;
    //display: block;
  }

  .dd-handle {
    transition: opacity 300ms, width 100ms;
    //margin-bottom: 0!important;
  }

  .item-detail-content {
    padding: 0 16px 0 46px;
    //padding: 8px 16px 8px 46px;
    //border: 1px solid rgba(120,130,140,0.21);
    background: white;
    z-index: 50;

    overflow: hidden;
    //height: 0;
    max-height: 0;

    transition: all 500ms;

    //display: none;
    transform: scale(1, 0);
  }

  &.open-detail {
    margin-bottom: 12px;

    .dd-item-backgroundWrap {
      opacity: 1;
      display: block;
    }
    .item-detail-link {
      z-index: -1;
    }

    .dd-handle {
      opacity: 0;
      width: 0;
    }

    .activ-controler {
      opacity: 0;
      width: 0;
      display: none;
    }

    .box-list {
      //height: 1px;
      max-height: 0;
      min-height: inherit;
      padding: 0;
      margin: 0!important;
      border: none;
      opacity: 0;


      transform: scale(1, 0);
    }

    .item-detail-content {

      //height: auto;
      max-height: 1200px;
      margin-top: -20px;


      transform: scale(1, 1);

    }

  }


}





