.dropify-wrapper .help-block {
  position : absolute;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: 4;
  transition: visibility 0s linear .15s,opacity .15s linear;
  background-color: rgba(243,65,65,.3);
}
.dropify-wrapper .help-block.text-danger {
  color: #f70b06;
}
.box-list__settings__one__media .dropify-wrapper .dropify-preview .dropify-render img {
  -webkit-transform: translate(-50%,-50%);
  transform: translate(-50%,-50%);
}

.timeBox {
  position: relative;
  display: block;
  padding: 0 50px 0 30px;
  margin-top: 8px;
  line-height: 36px;
}

.timeBox span.help {
  position: absolute;
  left: 0;
  line-height: 38px;
  top: 0;
}
.timeBox input {
  display: inline-block;
  width: 100%;
  top: 0;
}
.timeBox input[type=checkbox] {
  vertical-align: middle;
  height: auto;
  position: relative;
  z-index: 99;
  width: auto;
  padding-right: 0;
  left: auto;
  top: auto;
  margin-left: 10px;
}
.timeBox select {
  position: absolute;
  right: 0;
  width: 50px;
  top: 0;
  padding: 0;
}

.grido input[type="text"] {
  width: 100%;
}
.grido input[type="text"][data-provide="datepicker"] {
  width: 8em;
}


